
.intro{
	margin-left: 50px;
	box-sizing: border-box;
	padding-top: 50px;
	min-width: 1500px;
}
.intro .text{
	min-width: 1270px;
}
.intro .text p{
	font-size: 20px;
	font-family: "微软雅黑";
	margin-bottom: 20px;
	line-height: 30px;
	color:#FFEA43;
	width: 1240px
}
.intro .text p:nth-of-type(odd){
	color:#3471B0;
}
.intro img{
	float: right;
	margin-top: 50px;
	margin-right: 50px;
}



