
.body{
	height: calc(100vh - 100px);
	background-color: #95D0DE;
}
.body .nav{
	margin:50px 0;
	width: 250px;
	float: left;
	height: calc(100% - 100px);
	border-right:1px solid #000;
	box-sizing: border-box;
}
.body .nav h3{
	text-align: center;
	margin-top: 20px;
	margin-bottom: 50px;
	font-size: 30px;
	color:#fff;
	text-shadow: 1px 1px 5px #888888,
	2px 2px 5px #888888,
	3px 3px 5px #888888,
	4px 4px 5px #888888;
}
.body .nav ul{
	width: 150px;
	margin:0 auto;
}
.body .nav ul .router_li{
	border-bottom:1px solid #000;
	text-align: center;
	height: 40px;
	line-height: 40px;
}
.body .nav ul .router_li a{
	font-size: 18px;
}
.body .nav ul .router_li a:hover{
	color:#fff;
}
.body .view{
	width: calc(100vw - 267px);
	float: left;
	height: 100%;
	box-sizing: border-box;
	padding:50px 0;
}
.fade-enter-active {
	animation: bounce-in 0.7s;
}
.fade-leave-active {
	animation: bounce-out 0.7s;
}
@keyframes bounce-in {
0% {
		transform: scale(0);
}
50% {
		transform: scale(1.2);
}
100% {
		transform: scale(1);
}
}
@keyframes bounce-out {
0% {
		transform: scale(1);
}
50% {
		transform: scale(1.2);
}
100% {
		transform: scale(0);
}
}



