
.tips_wrap{margin-left:100px;margin-top:50px;height:100%;position:relative
}
.tips_wrap .btns{-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-user-select:none;display:inline-block;width:120px;height:40px;line-height:40px;text-align:center;background-color:#4CB034;color:#A0206B;font-size:20px;cursor:pointer;border-radius:14px
}
.tips_wrap .btns:hover{color:#fff
}
.tips_wrap .block{position:relative;margin-top:20px;height:40px;line-height:40px;text-align:center;color:#fff;font-size:20px;width:150px;background-color:yellowgreen;border-radius:14px;box-shadow:1px 1px 5px #0F3B7C, 2px 2px 5px #0F3B7C, 3px 3px 5px #0F3B7C, 4px 4px 5px #0F3B7C, 5px 5px 5px #0F3B7C, 6px 6px 5px #0F3B7C, 10px 10px 10px #000;transition:all 0.5s ease-out
}
.tips_wrap .l_div{position:absolute;left:0;top:50px
}
.tips_wrap .l_list-enter-active{left:0px
}
.tips_wrap .l_list-enter{left:-178px
}
.tips_wrap .l_list-leave-active{animation:l_out 1s;-webkit-animation:l_out 1s
}
@keyframes l_out{
0%{left:0
}
25%{left:20px
}
100%{left:-178px
}
}
.tips_wrap .r_div{position:absolute;right:0;top:50px
}
.tips_wrap .r_list-enter-active{left:0px
}
.tips_wrap .r_list-enter{left:178px
}
.tips_wrap .r_list-leave-active{animation:r_out 1s;-webkit-animation:r_out 1s
}
@keyframes r_out{
0%{left:0px
}
25%{left:-20px
}
100%{left:178px
}
}
.tips_wrap .t_div{position:absolute;left:calc(50% - 75px);top:0
}
.tips_wrap .t_list-enter-active{top:0px
}
.tips_wrap .t_list-enter{top:-40px
}
.tips_wrap .t_list-leave-active{animation:t_out 1s;-webkit-animation:t_out 1s
}
@keyframes t_out{
0%{top:0px
}
25%{top:20px;opacity:0.7
}
100%{top:25px;opacity:0.1
}
}
.tips_wrap .b_div{position:absolute;left:calc(50% - 75px);bottom:0
}
.tips_wrap .b_list-enter-active{bottom:0px
}
.tips_wrap .b_list-enter{bottom:-40px
}
.tips_wrap .b_list-leave-active{animation:b_out 1s;-webkit-animation:b_out 1s
}
@keyframes b_out{
from{transform:rotateZ(0) scale(1);-webkit-transform:rotateZ(0) scale(1)
}
to{transform:rotateZ(360deg) scale(0);-webkit-transform:rotateZ(360deg) scale(0)
}
}



